import uniPopup from '@/components/uni-popup/uni-popup.vue'
import avatar from "@/components/yq-avatar/yq-avatar.vue";
export default{
	components: {
		uniPopup,
		 avatar
	},
	data(){
		return{
			current:0,
			imgurl:["../../static/img/HeadImg.jpg"],
			show: false,
			type:"",
			sex:[{
				value:"1",
				name:"男"
			},{
				value:"2",
				name:"女"
			}],
			items:[]
		}
	},
	onShow() {
		//data，每次返回会刷新
		
	},
	methods:{
		setData(){
			console.log("重新请求，渲染页面")
		},
		radioChange: function(e) {
		    for (let i = 0; i < this.sex.length; i++) {
		        if (this.sex[i].value === e.target.value) {
		            this.current = i;
		            break;
		        }
		    }
		},
		togglePopup(type, open) {
			this.type = type;
			this.$refs[open].open();
		},
		cancel(type){
			this.$refs[type].close()
		},
		doBefore() {
			console.log('doBefore');
		},
		clk(index) {
			this.$refs.avatar.fChooseImg(index,{
				selWidth: '360upx', selHeight: '360upx', 
				expWidth: '280upx', expHeight: '280upx',
				inner: index ? 'true' : 'false'
			});
		},
		doUpload(rsp) {
			console.log(rsp);
			this.$set(this.imgurl, rsp.index, rsp.path);
			return;
			uni.uploadFile({
				url: '', //仅为示例，非真实的接口地址
				filePath: rsp.path,
				name: 'avatar',
				formData: {
					'avatar': rsp.path
				},
				success: (uploadFileRes) => {
					console.log(uploadFileRes.data);
				},
				complete(res) {
					console.log(res)
				}
			});
		},
		sexchange(type){
			this.cancel(type);
			//向后台发送数据
			this.setData();
			
		}
	}
}
